/* Estilo del fondo general */

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {

  height: 100dvh;
  width: 100%;
  font-family: Arial, sans-serif;
  background: linear-gradient(135deg, #7f00ff, #e100ff); /* Fondo tipo Udemy */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Contenedor principal */
.login-container {
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  position: relative;
}


/* Tarjeta del login */
.login-card {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  width: 100%;
  text-align: center;
}

/* Título */
.login-card h1 {
  margin-bottom: 1.5rem;
  color: #333;
}

/* Campos de entrada */
.login-card input {
  width: 100%;
  padding: 12px;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
}

/* Botón de ingresar */
.login-card button {
  background-color: #a435f0;
  color: white;
  border: none;
  padding: 12px;
  width: 100%;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.login-card button:hover {
  background-color: #922ed1;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(8px);
  background-color: rgba(0, 0, 0, 0.3); /* oscurece ligeramente */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 6px solid #cbd5e0;
  border-top: 6px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-text {
  margin-top: 15px;
  font-size: 1.1rem;
  font-weight: bold;
  color: #fff;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
